import request from '@/utils/request'

// 查询图书信息列表
export function listBook(query) {
  return request({
    url: '/project/book/list',
    method: 'get',
    params: query
  })
}

// 查询图书信息详细
export function getBook(data) {
  return request({
    url: '/project/book/detil' ,
    method: 'post',
    data:data
  })
}

// 新增图书信息
export function addBook(data) {
  return request({
    url: '/project/book/add',
    method: 'post',
    data: data
  })
}

// 修改图书信息
export function updateBook(data) {
  return request({
    url: '/project/book/edit',
    method: 'post',
    data: data
  })
}

// 删除图书信息
export function delBook(data) {
  return request({
    url: '/project/book/del',
    method: 'post',
    data: data
  })
}


